
import * as ContentArrayHtml from './ContentArrayHtml';
import * as ContentArrayPlain from './ContentArrayPlain';

export function render(format, content) {
   if (format === 'html') {
      ContentArrayHtml.render(content);
   } else {
      ContentArrayPlain.render(content);
   }
}

export function assignDeps(g) {
   g.Ch = ContentArrayHtml;
   g.Cp = ContentArrayPlain;
}
